* {
  box-sizing: border-box;
}

body {
  background-color: #000;
  font-family: Sans-serif, Arial, Calibri;
}

header {
  background-color: #000;
  padding: 30px;
  text-align: center;
  font-size: 30px;
  color: #f2951c;
}

h2 {
  color: #f2951c;
  font-family: "Roboto", Sans-serif;
  font-weight: 600;
}

input[type="text"] {
  border: none;
  width: 80%;
  max-width: 150px;
  padding: 8px 8px;
  box-sizing: border-box;
  border-bottom: #f2951c solid 1px;
  border-right: #f2951c solid 1px;
  border-radius: 4px;
  background-color: #000;
  color: #f2951c;
}

.wrapper {
  width: 1260px;
  margin: 0 auto;
  overflow: hidden;
}

.searchbar {
  width: 100%;
  max-width: 200px;
  margin: auto;
}

.container {
  float: left;
  margin: 10px auto;
  width: 100%;
  max-width: 855px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  border: #f2951c solid 0px;
  overflow: hidden;
}

.surface-containerLeft {
  float: left;
  width: auto;
  margin: 20px auto;
  background-color: #000;
  border: #f2951c solid 0px;
  clear: none;
  overflow: hidden;
  min-width: 160px;
}

.surface-containerRight {
  float: left;
  width: auto;
  margin: 20px auto;
  background-color: #000;
  border: #f2951c solid 0px;
  clear: none;
  overflow: hidden;
  min-width: 155px;
}

.item {
  flex-basis: 0;
  margin: 10px;
  margin-bottom: 30px;
  position: relative;
  -webkit-transition: opacity 0.3s ease;
  -o-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
  border-radius: 4px;
}

.item__img__img {
  border: solid 1px #f2951c;
  border-radius: 4px;
}
.item__img {
  border: 0;
  border-radius: 4px;
}

.item__rank {
  background-color: #f2951c;
  border-radius: 50%;
  font-size: 12px;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  top: -10px;
  right: -10px;
  font-weight: bold;
  -webkit-box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.4);
  box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.4);
  color: #000;
}

.item__name {
  display: block;
  font-size: 14px;
  font-weight: bold;
  padding: 0 2px;
  margin: 0;
  text-decoration: none;
  color: #f2951c;
}
.item__status {
  display: block;
  font-size: 12px;
  color: #f2951c;
  padding: 5px 2px;
  margin: 0px 2px 0;
  text-decoration: none;
}

.item__link:hover {
  text-decoration: none;
}

.ias-foot {
  clear: both;
  text-align: center;
  margin: 20px 0;
  font: 12px sans-serif;
  color: #aaa;
}

.ias-foot a {
  color: #999;
}

div.greyGridTable {
  right: 0;
  margin-left: 15px;
  margin-right: 15px;
  margin-bottom: 50px;
  background-color: #000000;
  text-align: left;
  border-collapse: collapse;
  width: 150px;
}
.tableRight {
  margin-left: auto;
  margin-right: 0;
}
.divTable.greyGridTable .divTableCell,
.divTable.greyGridTable .divTableHead {
  padding: 3px 4px;
}
.divTable.greyGridTable .divTableBody .divTableCell {
  font-size: 12px;
  color: #f2951c;
}
.divTable.greyGridTable .divTableHeading {
  background: #000000;
  border-bottom: 1px solid #f2951c;
}
.divTable.greyGridTable .divTableHeading .divTableHead {
  font-size: 14px;
  font-weight: bold;
  color: #f2951c;
  text-align: left;
}
.greyGridTable .tableFootStyle {
  font-size: 12px;
}
/* DivTable.com */
.divTable {
  display: table;
}
.divTableRow {
  display: table-row;
}
.divTableHeading {
  display: table-header-group;
}
.divTableCellRight {
  font-size: 12px;
  color: #f2951c;
  display: table-cell;
  text-align: right;
}

.divTableCellLeft {
  display: table-cell;
}

.divTableHead {
  display: table-cell;
}

.divTableHeading {
  display: table-header-group;
}
.divTableFoot {
  display: table-footer-group;
}
.divTableBody {
  display: table-row-group;
}

@media (max-width: 600px) {
  section {
    -webkit-flex-direction: column;
    flex-direction: column;
  }
}
